import * as ReactDOMClient from "react-dom/client";
import { App } from "./App";
import { worker } from "./mocks/worker";

worker.start({
  serviceWorker: {
    url: `${import.meta.env.BASE_URL}/mockServiceWorker.js`,
  },
});

const root = ReactDOMClient.createRoot(document.getElementById("root")!);
root.render(<App />);
